@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
:root {
  text-align: center;
  font: "Roboto", sans-serif;
  color: white;
}

.title {
  font-size: 4em;
}

.App {
  height: auto;
  background-image: linear-gradient(45deg, purple, orange);
  padding: 3ch;
  margin: 1.2em 1ch 1ch 1ch;
  border-radius: 2ch;
}

input {
  border: 0px;
  padding-left: 0.75ch;
  display: inline-block;
  height: 2em;
  width: 3em;
  margin: 1ch;
  border-radius: 0.5ch;
}

input[type="text"],
input[type="number"] {
  font-size: 2.5ch;
  font-family: "Roboto", monospace;
}

.num {
  width: 6em;
}

label {
  font-weight: 100;
  font-size: 3.5ch;
  display: block;
  margin: 1ch;
}

.selector {
  background: transparent;
}

h1 {
  font: "Roboto", sans-serif;
  font-size: 4.5ch;
}

